.footer {
  align-items: center;
  border-top: double 1.5rem black;
  display: flex;
  font-size: 1.5rem;
  grid-column: page-start/page-end;
  justify-content: space-evenly;
  padding: 2.5rem;
  width: 100%;
}
.footer-logo {
  display: flex;
  flex-direction: column;
  height: 18rem;
  justify-content: center;
  width: 18rem;
  .logo-img {
    height: 15rem;
    width: 15rem;
  }
}
.about-title {
  margin-bottom: 1rem;
}
.social {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 1rem;
  margin-top: 1rem;
  width: 100%;
  .facebook {
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    color: blue;
    height: 4rem;
    margin: 0.5rem;
    transition: 0.3s;
    width: 4rem;
    &:hover {
      cursor: pointer;
      transform: scale(1.2);
    }
  }
  .twitter {
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    color: rgba(105, 192, 233, 0.932);
    height: 4rem;
    margin: 0.5rem;
    transition: 0.3s;
    width: 4rem;
    &:hover {
      cursor: pointer;
      transform: scale(1.2);
    }
  }
  .instagram {
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    color: rgb(219, 61, 185);
    height: 4rem;
    margin: 0.5rem;
    transition: 0.3s;
    width: 4rem;
    &:hover {
      cursor: pointer;
      transform: scale(1.2);
    }
  }
}
@media (max-width: 600px) {
  .footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }
}
